
.container{width:1136px;margin:auto
}
.dbheader{display: -ms-flexbox;display: flex;display: -webkit-flex;padding:20px 0;-ms-flex-align:end;align-items:flex-end;
}
.dblogo,.dbsearch,.dbicon{-ms-flex: 1;flex: 1;-webkit-flex: 1;
}
.dbsearchcontent{display: -ms-flexbox;display: flex;display: -webkit-flex;border:1px solid rgb(242,70,91)
}
.dbsearchcontent .form-control{-ms-flex: 1;flex: 1;-webkit-flex: 1;
}
.dbsearchcontent span{width:80px;text-align: center;background: rgb(242,70,91);color:#fff;line-height: 34px;cursor: pointer;
}
.dbtab{text-align: center;
}
.dbtab ul{display: inline-block;
}
.dbtab ul li{display: inline-block;
}
.dbtab ul li a{display: inline-block;padding:14px;
}
.dbtab ul li a.router-link-active{color:rgb(242,70,91);border-bottom: 2px solid rgb(242,70,91);
}
.dbicon{text-align: right;
}
.dbicon img{width:80%
}
footer{padding:20px 0;text-align: center;background: #f1f2f4;margin-top: 80px;
}
.category{padding:20px
}
.category ul li{display: inline-block;padding:10px;cursor: pointer;
}
.category ul li.active{color:#FF4400
}
.reset{width: 32px;height: 32px;background: url(assets/img/close.png) no-repeat;background-size: cover;
}



